"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { v4 as uuidv4 } from "uuid";
import { db } from "..//firebaseConfig.js";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

type EducationHistory = EducationModel & {
  note: string;
};

export default function ModelListPage() {
  const [models, setModels] = useState<EducationModel[]>([]);
  const [history, setHistory] = useState<EducationHistory[]>([]);
  const [editId, setEditId] = useState<string | null>(null);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
    note: "",
  });
  const [sortOrder, setSortOrder] = useState<"newest" | "nameAsc">("newest");

  // Ë™≠„ÅøËæº„ÅøÔºölocalStorageÔºãFirestore ‰∏°Êñπ„ÄÅFirestoreÂÑ™ÂÖà„Åß„Éû„Éº„Ç∏
  useEffect(() => {
    async function loadAll() {
      // 1) localStorage
      const localModels: EducationModel[] = JSON.parse(
        localStorage.getItem("styleModels") || "[]"
      );
      const localHistory: EducationHistory[] = JSON.parse(
        localStorage.getItem("educationStylesHistory") || "[]"
      );

      // 2) Firestore
      let fsModels: EducationModel[] = [];
      let fsHistory: EducationHistory[] = [];
      try {
        const mSnap = await db
          .collection("styleModels")
          .orderBy("updatedAt", "desc")
          .get();
        fsModels = mSnap.docs.map((d) => ({
          ...(d.data() as Omit<EducationModel, "id">),
          id: d.id,
        }));
      } catch (e) {
        console.error("Firestore(styleModels)Ë™≠„ÅøËæº„Åø„Ç®„É©„Éº", e);
      }
      try {
        const hSnap = await db
          .collection("educationStylesHistory")
          .orderBy("updatedAt", "desc")
          .get();
        fsHistory = hSnap.docs.map((d) => ({
          ...(d.data() as Omit<EducationHistory, "id">),
          id: d.id,
        }));
      } catch (e) {
        console.error("Firestore(educationStylesHistory)Ë™≠„ÅøËæº„Åø„Ç®„É©„Éº", e);
      }

      // „Éû„Éº„Ç∏ÔºÜÈáçË§áÊéíÈô§ (FirestoreÂÑ™ÂÖà)
      const modelMap = new Map<string, EducationModel>();
      fsModels.concat(localModels).forEach((m) => modelMap.set(m.id, m));
      setModels(Array.from(modelMap.values()));

      const histMap = new Map<string, EducationHistory>();
      fsHistory.concat(localHistory).forEach((h) => histMap.set(h.id, h));
      setHistory(Array.from(histMap.values()));
    }
    loadAll();
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const startEdit = (m: EducationModel) => {
    setEditId(m.id);
    setForm({
      name: m.name,
      philosophy: m.philosophy,
      evaluationFocus: m.evaluationFocus,
      languageFocus: m.languageFocus,
      childFocus: m.childFocus,
      note: "",
    });
  };

  const cancelEdit = () => {
    setEditId(null);
    setForm({
      name: "",
      philosophy: "",
      evaluationFocus: "",
      languageFocus: "",
      childFocus: "",
      note: "",
    });
  };

  const handleSave = async () => {
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      alert("ÂøÖÈ†àÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
      return;
    }
    const now = new Date().toISOString();
    let updatedModels: EducationModel[];
    let id = editId!;
    if (editId) {
      // Êõ¥Êñ∞
      id = editId;
      try {
        await db
          .collection("styleModels")
          .doc(id)
          .update({ ...form, updatedAt: now });
      } catch (e) {
        console.error("Firestore„Çπ„Çø„Ç§„É´Êõ¥Êñ∞„Ç®„É©„Éº", e);
      }
      updatedModels = models.map((m) =>
        m.id === id ? { id, ...form, updatedAt: now } : m
      );
    } else {
      // Êñ∞Ë¶è
      id = uuidv4();
      const newModel: EducationModel = {
        id,
        name: form.name.trim(),
        philosophy: form.philosophy.trim(),
        evaluationFocus: form.evaluationFocus.trim(),
        languageFocus: form.languageFocus.trim(),
        childFocus: form.childFocus.trim(),
        updatedAt: now,
      };
      try {
        await db.collection("styleModels").doc(id).set(newModel);
      } catch (e) {
        console.error("Firestore„Çπ„Çø„Ç§„É´‰øùÂ≠ò„Ç®„É©„Éº", e);
      }
      updatedModels = [newModel, ...models];
    }
    setModels(updatedModels);
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));

    // Â±•Ê≠¥ËøΩÂä†
    const newHistoryEntry: EducationHistory = {
      id,
      updatedAt: now,
      name: form.name.trim(),
      philosophy: form.philosophy.trim(),
      evaluationFocus: form.evaluationFocus.trim(),
      languageFocus: form.languageFocus.trim(),
      childFocus: form.childFocus.trim(),
      note: form.note.trim() || "Ôºà„É°„É¢„Å™„ÅóÔºâ",
    };
    try {
      await db
        .collection("educationStylesHistory")
        .add(newHistoryEntry);
    } catch (e) {
      console.error("FirestoreÂ±•Ê≠¥‰øùÂ≠ò„Ç®„É©„Éº", e);
    }
    const updatedHistory = [newHistoryEntry, ...history];
    setHistory(updatedHistory);
    localStorage.setItem(
      "educationStylesHistory",
      JSON.stringify(updatedHistory)
    );

    cancelEdit();
  };

  const handleDelete = async (id: string) => {
    if (!confirm("„Åì„ÅÆ„É¢„Éá„É´„ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü")) return;
    try {
      await db.collection("styleModels").doc(id).delete();
    } catch (e) {
      console.error("Firestore„É¢„Éá„É´ÂâäÈô§„Ç®„É©„Éº", e);
    }
    const remaining = models.filter((m) => m.id !== id);
    setModels(remaining);
    localStorage.setItem("styleModels", JSON.stringify(remaining));
  };

  const sortedModels = () => {
    const copy = [...models];
    if (sortOrder === "newest") {
      return copy.sort(
        (a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime()
      );
    }
    return copy.sort((a, b) => a.name.localeCompare(b.name));
  };

  // „Çπ„Çø„Ç§„É´ÁúÅÁï•...

  return (
    <main style={{ padding: 24, fontFamily: "sans-serif", maxWidth: 900, margin: "0 auto" }}>
      {/* „Éä„Éì */}
      <nav style={{ display: "flex", gap: 16, overflowX: "auto", padding: 16, backgroundColor: "#f0f0f0", borderRadius: 8, marginBottom: 24 }}>
        <Link href="/" style={navLinkStyle}>üè† „Éõ„Éº„É†</Link>
        <Link href="/plan" style={navLinkStyle}>üìã ÊéàÊ•≠‰ΩúÊàê</Link>
        <Link href="/plan/history" style={navLinkStyle}>üìñ Ë®àÁîªÂ±•Ê≠¥</Link>
        <Link href="/practice/history" style={navLinkStyle}>üì∑ ÂÆüË∑µÂ±•Ê≠¥</Link>
        <Link href="/models/create" style={navLinkStyle}>‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê</Link>
        <Link href="/models" style={navLinkStyle}>üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß</Link>
        <Link href="/models/history" style={navLinkStyle}>üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥</Link>
      </nav>

      <h1 style={{ fontSize: 24, marginBottom: 16 }}>ÊïôËÇ≤Ë¶≥„É¢„Éá„É´‰∏ÄË¶ß„ÉªÁ∑®ÈõÜ</h1>

      {/* ‰∏¶„Å≥Êõø„Åà */}
      <label style={{ display: "block", marginBottom: 16 }}>
        ‰∏¶„Å≥Êõø„ÅàÔºö
        <select
          value={sortOrder}
          onChange={(e) => setSortOrder(e.target.value as any)}
          style={{ marginLeft: 8, padding: 4 }}
        >
          <option value="newest">Êñ∞ÁùÄÈ†Ü</option>
          <option value="nameAsc">ÂêçÂâçÈ†Ü</option>
        </select>
      </label>

      {/* Á∑®ÈõÜ„Éï„Ç©„Éº„É† */}
      {editId && (
        <section style={cardStyle}>
          <h2 style={{ marginTop: 0 }}>Á∑®ÈõÜ„É¢„Éº„Éâ</h2>
          <input
            placeholder="„É¢„Éá„É´Âêç"
            value={form.name}
            onChange={(e) => handleChange("name", e.target.value)}
            style={inputStyle}
          />
          <textarea
            placeholder="ÊïôËÇ≤Ë¶≥"
            rows={2}
            value={form.philosophy}
            onChange={(e) => handleChange("philosophy", e.target.value)}
            style={inputStyle}
          />
          <textarea
            placeholder="Ë©ï‰æ°Ë¶≥ÁÇπ„ÅÆÈáçË¶ñÁÇπ"
            rows={2}
            value={form.evaluationFocus}
            onChange={(e) => handleChange("evaluationFocus", e.target.value)}
            style={inputStyle}
          />
          <textarea
            placeholder="Ë®ÄË™ûÊ¥ªÂãï„ÅÆÈáçË¶ñÁÇπ"
            rows={2}
            value={form.languageFocus}
            onChange={(e) => handleChange("languageFocus", e.target.value)}
            style={inputStyle}
          />
          <textarea
            placeholder="ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßø"
            rows={2}
            value={form.childFocus}
            onChange={(e) => handleChange("childFocus", e.target.value)}
            style={inputStyle}
          />
          <textarea
            placeholder="Êõ¥Êñ∞„É°„É¢"
            rows={2}
            value={form.note}
            onChange={(e) => handleChange("note", e.target.value)}
            style={{ ...inputStyle, fontStyle: "italic" }}
          />
          <div style={{ marginTop: 16 }}>
            <button onClick={handleSave} style={buttonPrimary}>
              ‰øùÂ≠ò
            </button>
            <button onClick={cancelEdit} style={{ ...buttonPrimary, backgroundColor: "#757575" }}>
              „Ç≠„É£„É≥„Çª„É´
            </button>
          </div>
        </section>
      )}

      {/* „É¢„Éá„É´‰∏ÄË¶ß */}
      {sortedModels().length === 0 ? (
        <p>„Åæ„Å†„É¢„Éá„É´„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ</p>
      ) : (
        sortedModels().map((m) => (
          <div key={m.id} style={cardStyle}>
            <h3 style={{ marginTop: 0 }}>{m.name}</h3>
            <p><strong>ÊïôËÇ≤Ë¶≥Ôºö</strong> {m.philosophy}</p>
            <p><strong>Ë©ï‰æ°Ë¶≥ÁÇπÔºö</strong> {m.evaluationFocus}</p>
            <p><strong>Ë®ÄË™ûÊ¥ªÂãïÔºö</strong> {m.languageFocus}</p>
            <p><strong>ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇÔºö</strong> {m.childFocus}</p>
            <div style={{ marginTop: 16 }}>
              <button onClick={() => startEdit(m)} style={buttonPrimary}>
                Á∑®ÈõÜ
              </button>
              <button onClick={() => handleDelete(m.id)} style={{ ...buttonPrimary, backgroundColor: "#f44336" }}>
                ÂâäÈô§
              </button>
            </div>
          </div>
        ))
      )}
    </main>
);
}

// --- „Çπ„Çø„Ç§„É´ÂÆöÁæ© ---
const navLinkStyle: React.CSSProperties = {
  padding: "0.5rem 1rem",
  backgroundColor: "#1976d2",
  color: "white",
  borderRadius: 6,
  textDecoration: "none",
  fontWeight: "bold",
  whiteSpace: "nowrap",
};

const cardStyle: React.CSSProperties = {
  border: "1px solid #ccc",
  borderRadius: 12,
  padding: 16,
  marginBottom: 24,
  backgroundColor: "white",
  boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
};

const inputStyle: React.CSSProperties = {
  width: "100%",
  padding: 8,
  marginBottom: 12,
  fontSize: "1rem",
  borderRadius: 6,
  border: "1px solid #ccc",
  boxSizing: "border-box",
};

const buttonPrimary: React.CSSProperties = {
  backgroundColor: "#4CAF50",
  color: "white",
  padding: "8px 16px",
  border: "none",
  borderRadius: 6,
  cursor: "pointer",
  fontWeight: "bold",
  marginRight: 8,
};
